<template>
<div class="page-content">
  <el-table
    :data="leaveArr"
    style="width: 100%">
    <el-table-column
      prop="user_name"
      label="">
    </el-table-column>
    <el-table-column
      prop="user_phone"
      label="">
    </el-table-column>
    <el-table-column
      prop="class_start_at"
      label="">
    </el-table-column>
    <el-table-column
      prop="lesson_start_at"
      label="">
    </el-table-column>
     <el-table-column
      prop="lesson_end_at"
      label="">
    </el-table-column>
     <el-table-column
      prop="teacher_name"
      label="">
    </el-table-column>
    <el-table-column
    label="">
      <template slot-scope="scope">
        <el-button
          type="text"
          @click="handleEdit(scope.$index, scope.row)"></el-button>
          <el-divider direction="vertical"></el-divider>
        <el-button
          type="text"
          style="color: #f56c6c;"
          @click="handleDelete(scope.$index, scope.row)"></el-button>
      </template>
    </el-table-column>
  </el-table>
</div>
</template>
<script>
import leaveAxios from '@/global/service/leave.js'
export default {
  data () {
    return {
      leaveArr: []
    }
  },
  created () {
    leaveAxios.all().then(res => {
      this.leaveArr = res
    })
  },
  methods: {
    handleEdit (index, row) {
      const id = row.id
      const status = 1
      this.$confirm('')
        .then(_ => {
          leaveAxios.edit({ leave_id: id, status }).then(() => {
            this.leaveArr.splice(index, 1)
          })
        })
        .catch(_ => {})
    },
    handleDelete (index, row) {
      const id = row.id
      const status = 2
      this.$confirm('')
        .then(_ => {
          leaveAxios.edit({ leave_id: id, status }).then(() => {
            this.leaveArr.splice(index, 1)
          })
        })
        .catch(_ => {})
    }
  }
}
</script>
