<template>
<div class="page-content">
  <el-tabs v-model="activeName">
    <el-tab-pane label="" name="1">
      <div class="user-section" style="max-width:500px">
        <h3></h3>
        <el-form :label-position="labelPosition" :rules="rules" ref="rules"  label-width="100px" :model="user" >
        <el-form-item label="" prop="name">
          <el-input v-model.trim="user.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="phone">
          <el-input v-model.trim="user.phone"></el-input>
        </el-form-item>
        <el-form-item label="" prop="sex">
          <el-radio v-model.trim="user.sex" :label="1"></el-radio>
          <el-radio v-model.trim="user.sex" :label="2"></el-radio>
        </el-form-item>
        <el-form-item label="" prop="birthday">
          <el-date-picker
            v-model.trim="user.birthday"
            type="date"
            placeholder="">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="sms_name">
          <el-input v-model.trim="user.sms_name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="sms_phone">
          <el-input v-model.trim="user.sms_phone"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model.trim="user.site"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model.trim="user.school"></el-input>
        </el-form-item>
        </el-form>
        <el-button type="primary" @click="editUser"></el-button>
      </div>
    </el-tab-pane>
    <el-tab-pane label="" name="2">
      <h3> {{user.name}} : {{user.integral ? user.integral : 0}}</h3>
      <div style="width:500px;">
        <el-form :model="userIntegral" label-position="left" ref="userIntegral" label-width="70px" class="demo-ruleForm">
          <el-form-item
            label=""
            prop="integral"
            :rules="[
              { required: true, message: '', trigger: 'blur' },
              { type: 'number', message: ''}
            ]"
          >
            <el-input type="age" v-model.number="userIntegral.integral" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="integralEdit('add')"></el-button>
            <el-button @click="integralEdit('reduce')"></el-button>
          </el-form-item>
        </el-form>
      </div>
    </el-tab-pane>
    <el-tab-pane label="/" name="3">
      <h3> {{user.name}} : {{user.balance ? user.balance : 0}}</h3>
      <div style="width:500px;">
        <el-form :model="userTotal" label-position="left" ref="userTotal" label-width="70px" class="demo-ruleForm">
          <el-form-item
            label=""
            prop="total"
            :rules="[
              { required: true, message: '', trigger: 'blur' },
              {
                pattern: /^[1-9]\d*\,\d*|[1-9]\d*$/,
                message: '',
                trigger: 'blur'
              }
            ]"
          >
            <el-input type="age" v-model="userTotal.total" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item
            label=""
            prop="description"
            :rules="[
              { required: true, message: '', trigger: 'blur' },
            ]"
          >
            <el-input
              :rows="3"
              placeholder=""
              type="textarea"
              v-model="userTotal.description"
              resize="none"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="totalEdit('add')"></el-button>
            <el-button @click="totalEdit('reduce')"></el-button>
          </el-form-item>
        </el-form>
      </div>
    </el-tab-pane>
    <el-tab-pane label="" name="4">
      <h3></h3>
      <div style="max-width:500px">
        <el-form :label-position="labelPosition" :rules="planRules" ref="planRules"  label-width="160px" :model="planData" >
          <el-form-item label="" prop="plan_id">
            <el-cascader
            style="width:300px;"
            v-model="planData.plan_id"
            :options="combo_planArr">
            </el-cascader>
          </el-form-item>
          <el-form-item label="" prop="plan_price">
            <el-input v-model.number="planData.plan_price"></el-input>
          </el-form-item>
          <el-form-item label="" prop="extra_charge">
            <el-input v-model.number="planData.extra_charge"></el-input>
          </el-form-item>
          <el-form-item label="/">
            <el-input v-model.number="planData.Class_index"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model.number="planData.theOne_index"></el-input>
          </el-form-item>
          <el-form-item label="" prop="description">
            <el-input
            :rows="3"
            placeholder=""
            type="textarea"
            resize="none"
            v-model="planData.description"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="planAdd"></el-button>
          </el-form-item>
        </el-form>
      </div>
    </el-tab-pane>
    <el-tab-pane label="" name="5">
      <el-table
      :data="userPlanArr"
      style="width: 100%">
      <el-table-column
        prop="combo_name"
        label="">
      </el-table-column>
      <el-table-column
        prop="combo_plan_name"
        label="">
      </el-table-column>
      <el-table-column
        prop="course_name"
        label="">
      </el-table-column>
      <el-table-column
        prop="created_at"
        label="">
      </el-table-column>
      <el-table-column
        prop="plan_price"
        label="">
      </el-table-column>
      <el-table-column
        prop="extra_charge"
        label="">
      </el-table-column>
      <el-table-column
        prop="Class_index"
        label="">
      </el-table-column>
      <el-table-column
        prop="theOne_index"
        label="">
      </el-table-column>
      <el-table-column
        prop="description"
        :show-overflow-tooltip="true"
        label="">
      </el-table-column>
      <el-table-column
        label="">
        <template slot-scope="scope">
          <el-tag v-show="!scope.row.status" type="success"></el-tag>
          <el-tag v-show="scope.row.status == 1" type="info"></el-tag>
        </template>
      </el-table-column>
      <el-table-column
        label="">
        <template slot-scope="scope">
          <router-link :to="'/user/plan/'+scope.row.id">
          <el-button
          size="mini"></el-button>
          </router-link>
        <el-button
          size="mini"
          type="danger"
          style="margin-left:10px;"
          v-show="!scope.row.status"
          @click="handleDelete(scope.$index, scope.row)"></el-button>
      </template>
      </el-table-column>
    </el-table>
    </el-tab-pane>
    <el-tab-pane label="" name="6">
      <el-table
        :data="paymentArr">
        <el-table-column
          prop="created_at"
          label="">
        </el-table-column>
        <el-table-column
          prop="total"
          label="">
        </el-table-column>
        <el-table-column
          prop="description"
          :show-overflow-tooltip="true"
          label="">
        </el-table-column>
      </el-table>
    </el-tab-pane>
  </el-tabs>
</div>
</template>
<script>
import userAxios from '@/global/service/user.js'
import transformDate from '@/utils/authcode.js'
import DataStore from '@/global/storage/index.js'

export default {
  data () {
    return {
      activeName: '1',
      userId: '',
      labelPosition: 'left',
      user: {
        name: '',
        phone: '',
        sex: '',
        birthday: '',
        sms_name: '',
        sms_phone: '',
        site: '',
        school: ''
      },
      userIntegral: {
        integral: null
      },
      rules: {
        name: [
          { required: true, message: '', trigger: 'blur' }
        ],
        phone: [
          { required: true, message: '', trigger: 'blur' },
          {
            pattern: /^1[3456789]\d{9}$/,
            message: '',
            trigger: 'blur'
          }
        ],
        sex: [
          { required: true, message: '', trigger: 'blur' }
        ],
        birthday: [
          { required: true, message: '', trigger: 'blur' }
        ],
        sms_name: [
          { required: true, message: '', trigger: 'blur' }
        ],
        sms_phone: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      combo_planArr: [],
      planData: {
        plan_id: null,
        plan_price: null,
        extra_charge: null,
        Class_index: 0,
        theOne_index: 0,
        description: null
      },
      planRules: {
        plan_id: [
          { required: true, message: '', trigger: 'blur' }
        ],
        plan_price: [
          { required: true, message: '', trigger: 'blur' },
          { type: 'number', message: '', trigger: 'blur' }
        ],
        extra_charge: [
          { required: true, message: '', trigger: 'blur' },
          { type: 'number', message: '', trigger: 'blur' }
        ],
        description: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      userTotal: {
        total: null,
        description: null
      },
      userPlanArr: [],
      paymentArr: []
    }
  },
  created () {
    let id = this.userId = this.$route.params.id
    userAxios.show(id).then(res => {
      this.user = res.data
      this.paymentArr = res.paymentArr
    })
    userAxios.userAllPlan().then(res => {
      this.combo_planArr = res
    })
    this.getUserAllPlan()
  },
  methods: {
    handleDelete: function (index, row) {
      this.$confirm('')
        .then(_ => {
          userAxios.delUserIdPlan(row.id).then(() => {
            this.userPlanArr[index].status = 1
          })
        })
        .catch(_ => {})
    },
    getUserAllPlan () {
      userAxios.userIdAllPlan(this.userId).then(res => {
        this.userPlanArr = res
      })
    },
    editUser () {
      let id = this.userId
      let data = this.user
      if (data.birthday) {
        data.birthday = transformDate.formatDate(new Date(data.birthday))
      }
      this.$refs['rules'].validate(vaild => {
        if (vaild) {
          userAxios.edit(id, data)
        }
      })
    },
    integralEdit (type) {
      this.$refs['userIntegral'].validate(vaild => {
        if (vaild) {
          let data = {
            id: this.userId,
            index: this.userIntegral.integral
          }
          userAxios.integral(type, data).then(() => {
            if (type === 'add') {
              this.user.integral += this.userIntegral.integral
            } else {
              this.user.integral -= this.userIntegral.integral
            }
            this.userIntegral.integral = null
          })
        }
      })
    },
    planAdd () {
      this.$refs['planRules'].validate(vaild => {
        if (vaild) {
          let data = this.planData
          if (!data.Class_index && !data.theOne_index) return this.$message('')
          if (data.Class_index && data.theOne_index) return this.$message('')
          let Length = data.plan_id.length
          data.plan_id = data.plan_id[Length - 1]
          data.user_id = this.userId
          let managerId = DataStore.getManagerId()
          if (managerId) data.manager_id = managerId
          else return this.$router.replace({ name: 'login' })
          userAxios.userAddPlan(data).then(res => {
            this.user.balance -= this.planData.extra_charge
            this.getUserAllPlan()
            this.planData = {
              plan_id: null,
              plan_price: null,
              extra_charge: null,
              Class_index: 0,
              theOne_index: 0,
              description: null
            }
          })
        }
      })
    },
    totalEdit (type) {
      this.$refs['userTotal'].validate(vaild => {
        if (vaild) {
          let data = this.userTotal
          data.id = this.userId
          let managerId = DataStore.getManagerId()
          if (managerId) data.managerId = managerId
          else return this.$router.replace({ name: 'login' })
          userAxios.total(type, data).then(() => {
            if (type === 'add') {
              this.user.balance += Number(this.userTotal.total)
            } else {
              this.user.balance -= Number(this.userTotal.total)
            }
            this.userTotal.total = null
            this.userTotal.description = null
          })
        }
      })
    }
  }
}
</script>
