<template>
<div class="page-content">
  <div class="user-section" style="max-width:500px">
    <h3></h3>
    <el-form :label-position="labelPosition" :rules="rules" ref="rules"  label-width="100px" :model="user" >
      <el-form-item label="" prop="name">
        <el-input v-model.trim="user.name"></el-input>
      </el-form-item>
      <el-form-item label="" prop="phone">
        <el-input v-model.trim="user.phone"></el-input>
      </el-form-item>
      <el-form-item label="" prop="sex">
        <el-radio v-model.trim="user.sex" label="1"></el-radio>
        <el-radio v-model.trim="user.sex" label="2"></el-radio>
      </el-form-item>
      <el-form-item label="" prop="birthday">
        <el-date-picker
          v-model.trim="user.birthday"
          type="date"
          placeholder="">
        </el-date-picker>
      </el-form-item>
      <el-form-item label="" prop="sms_name">
        <el-input v-model.trim="user.sms_name"></el-input>
      </el-form-item>
      <el-form-item label="" prop="sms_phone">
        <el-input v-model.trim="user.sms_phone"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model.trim="user.site"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model.trim="user.school"></el-input>
      </el-form-item>
    </el-form>
    <el-button type="primary" @click="addUser"></el-button>
  </div>
</div>
</template>
<script>
import userAxios from '@/global/service/user.js'
import transformDate from '@/utils/authcode.js'
export default {
  data () {
    return {
      labelPosition: 'left',
      user: {
        name: '',
        phone: '',
        sex: '',
        birthday: '',
        sms_name: '',
        sms_phone: '',
        site: '',
        school: ''
      },
      rules: {
        name: [
          { required: true, message: '', trigger: 'blur' }
        ],
        phone: [
          { required: true, message: '', trigger: 'blur' },
          {
            pattern: /^1[3456789]\d{9}$/,
            message: '',
            trigger: 'blur'
          }
        ],
        sex: [
          { required: true, message: '', trigger: 'blur' }
        ],
        birthday: [
          { required: true, message: '', trigger: 'blur' }
        ],
        sms_name: [
          { required: true, message: '', trigger: 'blur' }
        ],
        sms_phone: [
          { required: true, message: '', trigger: 'blur' },
          {
            pattern: /^1[3456789]\d{9}$/,
            message: '',
            trigger: 'blur'
          }
        ]
      }
    }
  },
  methods: {
    addUser () {
      this.$refs['rules'].validate(vaild => {
        if (vaild) {
          let data = this.user
          if (data.birthday) data.birthday = transformDate.formatDate(data.birthday)
          userAxios.add(data).then(() => {
            this.$router.push({ path: '/user' })
          })
        }
      })
    }
  }
}
</script>
