<template>
<div class="page-content">
  <router-link to="/user/create">
    <el-button
          type="primary"></el-button>
  </router-link>
  <div style="margin:20px 0;">
    <el-input style="width:200px;" v-model="phone" placeholder=""></el-input>
     <el-button
     style="margin-left: 30px;"
      size="mini"
      @click="getPhone"
      type="primary"></el-button>
    <el-button type="danger"
      style="margin-left: 30px;"
      @click="clear"
      size="mini"></el-button>
  </div>
  <el-table
    :data="userArr"
    style="width: 100%;margin-bottom:20px;">
    <el-table-column
      prop="id"
      label="ID"
      width="80px;">
    </el-table-column>
    <el-table-column
      prop="phone"
      label=""
       width="130px;">
    </el-table-column>
    <el-table-column
      prop="name"
      label=""
      width="120px;">
    </el-table-column>
    <el-table-column
      prop="sexs"
      label=""
      width="80px;">
    </el-table-column>
    <el-table-column
      prop="sms_name"
      label=""
       width="120px;">
    </el-table-column>
    <el-table-column
      prop="sms_phone"
      label=""
      width="130px;">
    </el-table-column>
    <el-table-column
      prop="integral"
      label=""
       width="120px;">
    </el-table-column>
    <el-table-column
      prop="balance"
      label="">
    </el-table-column>
    <el-table-column label=""
    width="120px;">
      <template slot-scope="scope">
        <el-tag v-if="scope.row.status == 2"></el-tag>
        <el-tag v-if="scope.row.status == 1" type="danger"></el-tag>
      </template>
    </el-table-column>
    <el-table-column label="">
      <template slot-scope="scope">
        <router-link :to="'/user/Edit/' + scope.row.id" >
        <el-button
          size="mini"
          type="text"></el-button>
        </router-link>
        <el-divider v-if="scope.row.status == 1" direction="vertical"></el-divider>
        <el-button
          size="mini"
          type="text"
          v-if="scope.row.status == 1"
          @click="handleDelete(scope.$index, scope.row, 2)"></el-button>
           <el-divider  v-if="scope.row.status == 2" direction="vertical"></el-divider>
        <el-button
        size="mini"
        type="text"
        v-if="scope.row.status == 2"
        @click="handleDelete(scope.$index, scope.row, 1)"></el-button>
      </template>
    </el-table-column>
  </el-table>
  <el-pagination
    background
    layout="prev, pager, next"
    @current-change="current"
    :total="total">
  </el-pagination>
</div>
</template>
<script>
import userAxios from '@/global/service/user.js'
export default {
  data () {
    return {
      userArr: [],
      phone: '',
      newPhone: '',
      total: 0,
      pageSize: 10,
      currentPage: 1
    }
  },
  created () {
    this.getDate()
  },
  methods: {
    clear () {
      this.phone = ''
      this.newPhone = ''
      this.currentPage = 1
      this.getDate()
    },
    current (index) {
      this.currentPage = index
      this.getDate()
    },
    getPhone () {
      if (this.phone) {
        this.newPhone = this.phone
        this.getDate()
      } else {
        this.$message({
          message: '',
          type: 'warning'
        })
      }
    },
    handleDelete (index, data, status) {
      let text = status === 1 ? '' : '?'
      this.$confirm(text, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        userAxios.del(data.id, { status }).then(res => {
          this.userArr[index].status = status
        })
      }).catch(() => {})
    },
    getDate () {
      let data = {
        currentPage: this.currentPage,
        pageSize: this.pageSize
      }
      if (this.newPhone) {
        data.phone = this.newPhone
        data.currentPage = 1
      }
      userAxios.all(data).then(res => {
        this.total = res.total[0].total
        this.userArr = res.data.map(data => {
          data.sexs = data.sex === 1 ? '' : ''
          if (!data.integral) data.integral = 0
          if (!data.balance)data.balance = 0
          return data
        })
      })
    }
  }
}
</script>
