<template>
  <div class="page-content">
    <el-tabs tab-position="left">
    <el-tab-pane label="">
       <el-form :inline="true" :model="dateData" ref="dateRule" label-width="60px">
        <el-form-item label=""
        prop="date"
        :rules="[
          { required: true, message: ''},
          { type: 'date', message: ''}
        ]">
          <el-date-picker
          v-model="dateData.date"
          type="date"
          placeholder="">
        </el-date-picker>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="getlesso"></el-button>
        </el-form-item>
        <el-table
          :data="lessonArr"
          style="width: 100%">
          <el-table-column
            prop="start_at"
            label="">
          </el-table-column>
          <el-table-column
            prop="end_at"
            label="">
          </el-table-column>
          <el-table-column
            prop="teacher_name"
            label="">
          </el-table-column>
          <el-table-column
            label="">
            <template slot-scope="scope">
              <el-button
              size="mini"
              type="primary"
              style="margin-left:10px;"
              v-show="!scope.row.status"
              @click="addUserLesson(scope.$index, scope.row,1)"></el-button>
              <el-button
              size="mini"
              type="primary"
              style="margin-left:10px;"
              v-show="!scope.row.status"
              @click="addUserLesson(scope.$index, scope.row,2)"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-form>
    </el-tab-pane>
    <el-tab-pane label="">
      <div class="class_lesson-index">
        <p>:</p>
        <el-tag style="margin-left:20px;">{{allUserLesson.length}}</el-tag>
      </div>
      <el-table
        :data="allUserLesson"
        style="width: 100%">
        <el-table-column
          prop="class_name"
          label="">
        </el-table-column>
        <el-table-column
          prop="start_at"
          label="">
        </el-table-column>
        <el-table-column
          prop="end_at"
          label="">
        </el-table-column>
        <el-table-column
          prop="teacher_name"
          label="">
        </el-table-column>
        <el-table-column
          label="">
          <template slot-scope="scope">
           <span v-show="scope.row.user_lesson_status == 1"></span>
          <span v-show="scope.row.user_lesson_status == 2"></span>
          <span v-show="scope.row.user_lesson_status == 3"></span>
          </template>
        </el-table-column>
         <el-table-column
          label="">
          <template slot-scope="scope">
            <span>{{scope.row.lesson_status ? '' : ''}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="">
          <template slot-scope="scope">
            <router-link :to="'/lesson/'+scope.row.lesson_id">
              <el-button
              size="mini"></el-button>
            </router-link>
          </template>
        </el-table-column>
      </el-table>
    </el-tab-pane>
  </el-tabs>
  </div>
</template>
<script>
import classAxios from '@/global/service/class.js'
import userAxios from '@/global/service/user.js'

export default {
  data () {
    return {
      dateData: {
        date: ''
      },
      lessonArr: [],
      allUserLesson: []
    }
  },
  created () {
    this.getUserPlan()
  },
  methods: {
    getUserPlan () {
      let id = this.$route.params.id
      userAxios.allUserLessonID(id).then(res => {
        this.allUserLesson = res
      })
    },
    getlesso () {
      this.$refs['dateRule'].validate(valid => {
        if (valid) {
          let date = this.dateData
          classAxios.classReferShow(date).then(res => {
            this.lessonArr = res
          })
        }
      })
    },
    addUserLesson: function (index, row, className) {
      let data = {
        user_plan_id: this.$route.params.id,
        class_name: className,
        lesson_id: row.id
      }
      userAxios.addUserLesson(data).then(res => {
      })
    }
  }
}
</script>
<style lang="less" scoped>
.class_lesson-index{
  display: flex;
  align-items: center;
}
</style>
