<template>
  <div class="page-content">
    <router-link to="/manager/create" >
        <el-button
          type="primary"></el-button>
      </router-link>
    <el-table
      :data="managerArr"
      style="width: 100%">
      <el-table-column
        prop="name"
        label="">
      </el-table-column>
      <el-table-column
        prop="phone"
        label="">
      </el-table-column>
      <el-table-column
        prop="statuss"
        label="">
      </el-table-column>
      <el-table-column
        prop="created_at"
        label="">
      </el-table-column>
      <el-table-column label="">
      <template slot-scope="scope">
        <router-link :to="'/manager/Edit/' + scope.row.id" >
        <el-button
          size="mini"
          type="primary"></el-button>
        </router-link>
        <el-button
          size="mini"
          type="danger"
          style="margin-left:10px;"
          @click="handleDelete(scope.$index, scope.row)"></el-button>
      </template>
    </el-table-column>
    </el-table>
  </div>
</template>
<script>
import managerAxios from '@/global/service/manager.js'
export default {
  data () {
    return {
      managerArr: []
    }
  },
  created () {
    managerAxios.all().then(res => {
      this.managerArr = res.map(data => {
        data.statuss = data.status === 0 ? '' : ''
        return data
      })
    })
  },
  methods: {
    handleDelete (index, data) {
      managerAxios.del(data.id).then(res => {
        this.$message({
          message: '',
          type: 'success'
        })
        this.managerArr.splice(index, 1)
      })
    }
  }
}
</script>
