<template>
<div class="page-content">
  <div class="manager-section" style="max-width:500px">
    <h3></h3>
    <el-form :label-position="labelPosition" :rules="rules" ref="rules"  label-width="80px" :model="manager" >
      <el-form-item label="" prop="name">
        <el-input v-model.trim="manager.name"></el-input>
      </el-form-item>
      <el-form-item label="" prop="phone">
        <el-input v-model.trim="manager.phone"></el-input>
      </el-form-item>
      <el-form-item label="" prop="password">
        <el-input v-model.trim="manager.password" show-password></el-input>
      </el-form-item>
      <el-form-item label="" prop="status">
        <el-radio v-model.trim="manager.status" :label="Number(0)"></el-radio>
        <el-radio v-model.trim="manager.status" :label="Number(1)"></el-radio>
      </el-form-item>
    </el-form>
    <el-button type="primary" @click="editManager"></el-button>
  </div>
</div>
</template>

<script>
import managerAxios from '@/global/service/manager.js'
export default {
  data () {
    return {
      labelPosition: 'left',
      manager: {
        name: '',
        phone: '',
        password: '',
        status: null
      },
      rules: {
        name: [
          { required: true, message: '', trigger: 'blur' }
        ],
        phone: [
          { required: true, message: '', trigger: 'blur' },
          {
            pattern: /^1[3456789]\d{9}$/,
            message: '',
            trigger: 'blur'
          }
        ],
        password: [{
          required: true, message: '', trigger: 'blur'
        }],
        status: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  created () {
    let id = this.$route.params.id
    managerAxios.show(id).then(res => {
      this.manager = res[0]
    }).catch(e => {
      this.$message.error('')
      this.$router.push({ path: '/manager' })
    })
  },
  methods: {
    editManager () {
      let id = this.$route.params.id
      this.$refs['rules'].validate(vaild => {
        if (vaild) {
          managerAxios.edit(id, this.manager).then(res => {
            this.$message({
              message: '',
              type: 'success'
            })
            this.$router.push({ path: '/manager' })
          }).catch(e => {
            this.$message.error('')
          })
        }
      })
    }
  }
}
</script>
